import {Component, OnInit} from '@angular/core';
import {NoticeRepository} from "../../../service/notice-repository.service";
import {Notice} from "../../../model/notice.model";

@Component({
  selector: 'app-notice-board',
  templateUrl: './notice-board.component.html',
  styleUrls: ['./notice-board.component.css']
})
export class NoticeBoardComponent implements OnInit {
  currentNotice: Notice;
  notices: Notice[];

  constructor(private noticeRepository: NoticeRepository) {
  }

  ngOnInit() {
    this.getNotices();
  }

  getNotices(): void {
    this.noticeRepository.all()
      .subscribe(notices => {
        this.notices = notices;
        this.currentNotice = this.notices[0];
      });
  }

  isLastNotice() {
    return this.notices[this.notices.length - 1] == this.currentNotice;
  }

  isFirstNotice() {
    return this.notices[0] == this.currentNotice;
  }

  beforeNotice() {
    this.currentNotice = this.notices[this.currentIndex() - 1];
  }

  nextNotice() {
    this.currentNotice = this.notices[this.currentIndex() + 1];
  }

  currentIndex(): number {
    return this.notices.findIndex(notice => notice.id == this.currentNotice.id);
  }
}
