import {Injectable} from "@angular/core";
import {DataSource} from "./data-source.service";
import {Observable} from "rxjs/observable";
import {Domain} from "../model/domain.model";
import {HttpParams} from "@angular/common/http";
import {BY_NAME} from "../app.tokens";

@Injectable()
export class DomainRepository {
  private readonly domainUrl: string = '/domain/';

  constructor(private dataSource: DataSource){}

  all(): Observable<Domain[]> {
    return this.dataSource.get(this.domainUrl);
  }

  byName(name: string): Observable<Domain> {
    return this.dataSource.get(this.domainUrl,
      new HttpParams()
        .set(BY_NAME.key, BY_NAME.value)
        .set('value', name));
  }

}
