import {Component, OnInit} from '@angular/core';
import {filter, map, switchMap } from "rxjs/internal/operators";
import {forkJoin} from "rxjs/index";
import {ActivatedRoute, Params} from "@angular/router";
import {Domain} from "../../model/domain.model";
import {DomainRepository} from "../../service/domain-repository.service";
import {CategoryRepository} from "../../service/category-repository.service";
import {TopicRepository} from "../../service/topic-repository.service";
import {Topic} from "../../model/topic.model";
import {Category} from "../../model/category.model";

@Component({
  selector: 'app-category-introduction',
  templateUrl: './category-introduction.component.html',
  styleUrls: ['./category-introduction.component.css']
})
export class CategoryIntroductionComponent implements OnInit {
  private readonly domainName: string = 'Category Introduction';
  domain: Domain;
  category: Category;
  topics: Topic[];

  constructor(private domainRepository: DomainRepository,
              private categoryRepository: CategoryRepository,
              private topicRepository: TopicRepository,
              private route: ActivatedRoute) {
    this.domainRepository
      .byName(this.domainName)
      .subscribe(_domain => this.domain = _domain);

    this.route.params.pipe(
      filter((params: Params) => params['categoryName']),
      map((params: Params) => params['categoryName']),
      switchMap((categoryName: string) =>
        forkJoin(this.categoryRepository.byName(categoryName), this.topicRepository.byCategoryName(categoryName)))
    )
      .subscribe(([_category, _topics]) => {
        this.category = _category;
        this.topics = _topics;
      })
  }

  getFirstTopic(): Topic {
    return this.topics[0];
  }

  ngOnInit() {
  }

}
