import {Component, Input, OnInit, ViewChild} from '@angular/core';
import {Topic} from "../../../model/topic.model";
import {MatPaginator, MatTableDataSource} from "@angular/material";

@Component({
  selector: 'app-topic-table',
  templateUrl: './topic-table.component.html',
  styleUrls: ['./topic-table.component.css']
})
export class TopicTableComponent implements OnInit{
  @Input() categoryName: string;
  _topics: Topic[];
  @ViewChild(MatPaginator) paginator: MatPaginator;

  @Input()
  set topics(topics: Topic[]) {
    if(topics != null) {
      this._topics = topics;
      this.dataSource = new MatTableDataSource<Topic>(topics);
      this.dataSource.paginator = this.paginator;
      console.log(this.dataSource);
    }
  }

  get topics() {
    return this._topics;
  }

  displayedColumns = ['id', 'name', 'shortDescription', 'created'];
  dataSource;

  constructor() {}

  ngOnInit() {
  }

  resolvePath(element): string {
    return '/category/' + this.categoryName + '/' + element.name;
  }

  resolveParams(element): any {
    return { topicId: element.id }
  }
}
