import {Injectable} from "@angular/core";
import {Observable} from "rxjs/observable";
import {Youtube} from "../model/Youtube.model";
import {HttpClient} from "@angular/common/http";
import 'rxjs/operators/map'
import {map} from "rxjs/internal/operators";

@Injectable()
export class YoutubeService {
  private readonly youtubeUrl: string = '/youtube';
  constructor(private http: HttpClient){}

  get(videoId: string): Observable<Youtube> {
    if(videoId == null) {return;}
    return this.http
      .get(`/rest${this.youtubeUrl}/${videoId}`)
      .pipe(
        map(response => {
          console.log(response);
          return new Youtube(
            response['id'],
            response['snippet'].title,
            response['snippet'].description,
            response['snippet'].publishedAt,
            response['snippet'].thumbnails.high.url,
            response['videoUrl']) }));
  }
}
