import {Component, OnInit} from "@angular/core";
import {switchMap, tap} from "rxjs/internal/operators";
import {Domain} from "../../model/domain.model";
import {Category} from "../../model/category.model";
import {DomainRepository} from "../../service/domain-repository.service";
import {CategoryRepository} from "../../service/category-repository.service";

@Component({
  selector: 'app-category-main',
  templateUrl: './category-main.component.html',
  styleUrls: ['./category-main.component.css']
})
export class CategoryMainComponent implements OnInit {
  private readonly domainName = 'category';
  domain: Domain;
  categories: Category[];

  constructor(private domainRepository: DomainRepository,
              private categoryRepository: CategoryRepository) {
    this.domainRepository
      .byName(this.domainName)
      .pipe(
        tap((domain: Domain) => this.domain = domain),
        switchMap(() => this.categoryRepository.all())
      )
      .subscribe((_categories: Category[]) => this.categories = _categories);
  }


  ngOnInit() {
  }

}
