import {Injectable} from "@angular/core";
import {DataSource} from "./data-source.service";
import {Observable} from "rxjs/observable";
import {BY_NAME, BY_SEARCH, REQUEST_TOPICLISTS, TERM_ANY} from "../app.tokens";
import {Category} from "../model/category.model";
import {TopicList} from "../model/topic-list.model";
import {HttpParams} from "@angular/common/http";

/**
 * @author SiWoo Kim,
 * @email sm123tt@gmail.com
 * @version 1.0.0
 * @since 2018-05-06 오전 9:50
 * @github : https://github.com/Siwoo-Kim
 **/

@Injectable()
export class CategoryRepository {
  private readonly categoryUrl: string = 'category';

  constructor(private dataSource: DataSource) {}

  all(): Observable<Category[]> {
    return this.dataSource.get(this.categoryUrl);
  }

  byName(name: string): Observable<Category> {
    return this.dataSource.get(this.categoryUrl,
      new HttpParams()
        .set(BY_NAME.key, BY_NAME.value)
        .set('value', name));
  }

  allTopicLists(): Observable<TopicList[]> {
    return this.dataSource.get(this.categoryUrl,
      new HttpParams()
        .set(REQUEST_TOPICLISTS.key, REQUEST_TOPICLISTS.value));
  }

  searchAny(value: string): Observable<Category[]> {
    return this.dataSource.get(this.categoryUrl,
      new HttpParams()
        .set(BY_SEARCH.key, BY_SEARCH.value)
        .set(TERM_ANY.key, TERM_ANY.value)
        .set('value', value));
  }

}
