import {Component, OnInit} from '@angular/core';
import {switchMap, tap} from "rxjs/internal/operators";
import {Domain} from "../model/domain.model";
import {DomainRepository} from "../service/domain-repository.service";
import {CategoryRepository} from "../service/category-repository.service";
import {TopicList} from "../model/topic-list.model";

@Component({
  selector: 'app-category-bootstrap',
  templateUrl: './category-bootstrap.component.html',
  styleUrls: ['./category-bootstrap.component.css']
})
export class CategoryBootstrapComponent implements OnInit {
  private readonly domainName:string = 'category';
  domain: Domain;
  topicLists: TopicList[];

  constructor(private domainRepository: DomainRepository,
              private categoryRepository: CategoryRepository) {
    this.domainRepository
      .byName(this.domainName)
      .pipe(
        tap((domain: Domain) => this.domain = domain),
        switchMap(() => this.categoryRepository.allTopicLists())
      )
      .subscribe((_topicLists: TopicList[]) => this.topicLists= _topicLists);
  }

  ngOnInit() {
  }

}
