import {InjectionToken} from "@angular/core";

/*
*  ENV TOKENS
*/

export const BACKEND_URL_TOKEN = new InjectionToken('Backend url token');

export const DEV_BACKEND_URL = '/rest/';
export const PROD_BACKEND_URL = 'https://' + location.hostname + '/rest/';


export class Param {
  constructor(public key?: string,
              public value?: string){}
}

export const BY_NAME = new Param("by","name");
export const BY_CATEGORY = new Param("by","category");
export const BY_TOPIC = new Param("by","topic");
export const BY_SEARCH = new Param("by","search");
export const TERM_ANY = new Param("term","any");
export const REQUEST_TOPICLISTS = new Param("request","topicLists");


