import {Inject, Injectable} from "@angular/core";
import {HttpClient, HttpParams} from "@angular/common/http";
import {BACKEND_URL_TOKEN} from "../app.tokens";
import {Observable} from "rxjs/observable";
import {catchError} from "rxjs/internal/operators";
import {of} from 'rxjs/observable/of'
import {MatSnackBar} from "@angular/material";

/**
 * @author SiWoo Kim,
 * @email sm123tt@gmail.com
 * @version 1.0.0
 * @since 2018-05-06 오전 8:43
 * @github : https://github.com/Siwoo-Kim
 **/

@Injectable()
export class DataSource {

  constructor(private http: HttpClient,
              private snackBar: MatSnackBar,
              @Inject(BACKEND_URL_TOKEN) private backendUrl: string){}

  get<T>(url: string, params?: HttpParams): Observable<T> {
    return this.http.get<T>
      (this.resolveUrl(url), {params: params})
      .pipe(
        catchError(this.handleError('GET'))
      );
  }

  private resolveUrl(url: string) {
    if(url.startsWith('/')) {
      url = url.substr(1,url.length );
    }
    return this.backendUrl + url ;
  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      //Auauthrozied Handle Operation
      console.log(error.status == 401);
      if(error.status == 401) {
        this.snackBar.open('You have not authorized. Please Login', null,{
          duration: 2000,
          horizontalPosition: 'end',
          verticalPosition: 'top'
        })
      }


      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

}
