import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {SharedModule} from "../shared/shared.module";
import {CategoryBootstrapComponent} from './category-bootstrap.component';
import {ServiceModule} from "../service/service.module";
import {CategoryTimelineComponent} from './category-main/category-timeline/category-timeline.component';
import {CategoryMainComponent} from './category-main/category-main.component';
import {RouterModule, Routes} from "@angular/router";
import {CategoryIntroductionComponent} from './category-introduction/category-introduction.component';
import {TopicTableComponent} from './category-introduction/topic-table/topic-table.component';
import {CategoryMenuBarComponent} from './category-menu-bar/category-menu-bar.component';
import {CategoryTopicListComponent} from "./category-toplic-list/category-topic-list.component";
import {CategoryDescriptionComponent} from './category-introduction/category-description/category-description.component';
import {TopicComponent} from './topic/topic.component';
import {TopicHeaderComponent} from './topic/topic-header/topic-header.component';
import {ParagraphComponent} from './topic/paragraph/paragraph.component';

const routes: Routes = [
  {
    path: 'category', component: CategoryBootstrapComponent,
    children: [
      {path: 'main', component: CategoryMainComponent},
      {path: '', redirectTo: 'main', pathMatch: 'full'},
      {path: ':categoryName', component: CategoryIntroductionComponent},
      {path: ':categoryName/:topicName', component: TopicComponent},
    ]
  }
];

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    ServiceModule,
    RouterModule.forChild(routes),
  ],
  declarations: [
    /*Side Components*/
    CategoryMenuBarComponent,
    CategoryTopicListComponent,
    CategoryTimelineComponent,
    TopicTableComponent,
    /*Main Components*/
    CategoryMainComponent,
    CategoryIntroductionComponent,

    /*Bootstrap Component*/
    CategoryBootstrapComponent,
    CategoryDescriptionComponent,
    TopicComponent,
    TopicHeaderComponent,
    ParagraphComponent,
  ],
  exports: [
    RouterModule
  ]
})
export class CategoryModule {
}
