import {Component, Input, OnInit} from '@angular/core';
import {Category} from "../../../model/category.model";
import {BasicTime} from "../../../model/basic-time.model";

@Component({
  selector: 'app-category-timeline',
  templateUrl: './category-timeline.component.html',
  styleUrls: ['./category-timeline.component.css']
})
export class CategoryTimelineComponent implements OnInit {
  _categories: Category[];

  @Input()
  set categories(categories: Category[]) {
    if (categories != null && categories.length > 1) {
      this._categories = categories
        .sort((a: Category, b: Category) => BasicTime.sortByLastUpdatedDesc(a.basicTime, b.basicTime));
    }
  }

  get categories() {
    return this._categories;
  }

  constructor() {
  }

  ngOnInit() {
  }

}
