import {Component, OnInit} from '@angular/core';
import {forkJoin} from "rxjs/index";
import 'rxjs/operators/merge';
import {DomainRepository} from "../../../service/domain-repository.service";
import {Domain} from "../../../model/domain.model";

@Component({
  selector: 'app-main-board',
  templateUrl: './main-board.component.html',
  styleUrls: ['./main-board.component.css']
})
export class MainBoardComponent implements OnInit {
  private readonly categoryDomainName: string = 'Category';
  private readonly guideDomainName: string = 'Guides';
  domains: Domain[] = [];

  constructor(private domainRepository: DomainRepository) {}

  ngOnInit() {
    this.getMainDomains();
  }

  private getMainDomains() {
    forkJoin(this.domainRepository.byName(this.categoryDomainName), this.domainRepository.byName(this.guideDomainName))
      .subscribe(([domain1, domain2]) => {
        this.domains.push(domain1);
        this.domains.push(domain2);
        console.log(this.domains)
      })
  }
}
