import {Component, OnInit} from '@angular/core';
import {AuthService} from "../../service/auth-service.service";
import {Login} from "../../model/blogger.model";
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {Router} from "@angular/router";
import {Location} from "@angular/common";
import {SocialWrapper} from "../../model/social.model";

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  form: FormGroup;
  socials: SocialWrapper;

  constructor(private authService: AuthService,
              private router: Router,
              private location: Location,
              private fb: FormBuilder) {
    this.form = this.build();
  }

  ngOnInit() {
    this.getSocials();
  }

  build(): FormGroup {
    return this.fb.group({
      email: ['', [Validators.email, Validators.required]],
      password: ['', Validators.required],
    })
  }

  login() {
    this.authService
      .authenticate(new Login(this.form.value['email'], this.form.value['password']))
      .subscribe(authenticate => {
        console.log(authenticate);
        if (authenticate) {
          this.router.navigateByUrl('/');
        }
      }, (error) => {
        this.form.setErrors({credentials: true});
        console.log(this.form.hasError('credentials'));
      });
  }

  goback(): void {
    this.location.back();
  }

  getSocials() {
    this.authService.socials().subscribe(socials => this.socials = socials);
  }
}
