import {Component, ElementRef, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from "@angular/router";
import {map, switchMap} from "rxjs/internal/operators";
import {forkJoin} from "rxjs/index";
import {DomainRepository} from "../../service/domain-repository.service";
import {Domain} from "../../model/domain.model";
import {Topic} from "../../model/topic.model";
import {TopicRepository} from "../../service/topic-repository.service";

import {Youtube} from "../../model/Youtube.model";
import {Paragraph} from "../../model/paragraph.model";
import {ParagraphRepository} from "../../service/paragraph-repository.service";
import {MatSnackBar} from "@angular/material";

declare var $: any;

@Component({
  selector: 'app-topic',
  templateUrl: './topic.component.html',
  styleUrls: ['./topic.component.css']
})
export class TopicComponent implements OnInit {
  private readonly domainName: string = 'topic';
  youtube: Youtube;
  topic: Topic;
  hasNextTopic: boolean = true;
  paragraphs: Paragraph[];

  constructor(private domainRepository: DomainRepository,
              private topicRepository: TopicRepository,
              private paragraphRepository: ParagraphRepository,
              private router: Router,
              public snackBar: MatSnackBar,
              private route: ActivatedRoute) {
    this.route.queryParams.pipe(
      map(params => params['topicId']),
      switchMap(topicId => forkJoin(this.topicRepository.byId(topicId), this.paragraphRepository.byTopic(topicId))),
    ).subscribe(([topic, paragraphs]) => {
        this.hasNextTopic = true;
        this.topic = topic;
        this.paragraphs = paragraphs;
      }
    );
  }

  ngOnInit() {
  }

  nextTopic() {
    this.topicRepository
      .nextTopic(this.topic)
      .subscribe(topic => {
        if (topic != null) {
          this.router.navigateByUrl('/category/' + this.topic.category + '/' + topic.name + "?topicId=" + topic.id);
          window.scroll(0, 0);
        } else {
          this.hasNextTopic = false;
          this.snackBar.open('Sorry ! The topic is last topic! ', null, {
            duration: 2000
          });
        }
      })

  }
}
