import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {FormControl} from "@angular/forms";
import {debounceTime, distinctUntilChanged, filter, switchMap, tap} from "rxjs/internal/operators";
import {CategoryRepository} from "../../service/category-repository.service";
import {TopicRepository} from "../../service/topic-repository.service";
import {forkJoin} from "rxjs/index";
import {Topic} from "../../model/topic.model";
import {Category} from "../../model/category.model";
import {AuthService} from "../../service/auth-service.service";

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  @Output() sidenavChange:EventEmitter<void> = new EventEmitter<void>();
  _searchHidden: boolean = true;
  searchInput: FormControl = new FormControl("");

  topics: Topic[];
  categories: Category[];
  constructor(private categoryRepository: CategoryRepository,
              private topicRepository: TopicRepository,
              public authService: AuthService) {
    this.searchInput.valueChanges
      .pipe(
        debounceTime(800),
        distinctUntilChanged(),
        filter(value => value != null && value.length > 2),
        tap(value => console.log(value)),
        switchMap(value =>
          forkJoin(this.categoryRepository.searchAny(value),
            this.topicRepository.searchAny(value))))
      .subscribe(([_categories, _topics]) => {
        this.categories = null;
        this.topics = null;

        this.categories = _categories;
        this.topics = _topics;
    })
  }

  ngOnInit() {
  }

  hasResult(): boolean {
    return (this.topics != null && this.topics.length != 0 )
      || ( this.categories != null && this.categories.length != 0);
  }

  set searchHidden(hidden: boolean) {
    if(hidden) {
      setTimeout(()=>{
        this._searchHidden = hidden;
      },800);
    } else {
      this._searchHidden = hidden;
    }
  }

  get searchHidden(): boolean {
    return this._searchHidden;
  }

  logout() {
    this.authService.logout();
  }
}
