export class BasicTime {
  constructor(public created: string,
              public updated: string,
              public lastUpdated: string) {}

  static sortByLastUpdatedDesc(time1: BasicTime, time2: BasicTime): number {
    return new Date(Date.parse(time2.lastUpdated)).valueOf() - new Date(Date.parse(time1.lastUpdated)).valueOf();
  }


}
