import {Component, Input, OnInit} from '@angular/core';
import {Domain} from "../../model/domain.model";
import {DomainRepository} from "../../service/domain-repository.service";

@Component({
  selector: 'app-about-domain',
  templateUrl: './about-domain.component.html',
  styleUrls: ['./about-domain.component.css']
})
export class AboutDomainComponent implements OnInit {
  @Input() domain: Domain;

  @Input()
  set domainName(domainName: string) {
    this.domainRepository.byName(domainName)
      .subscribe(_domain => this.domain = _domain);
  };


  constructor(private domainRepository: DomainRepository) {

  }

  ngOnInit() {
  }

}
