import {Injectable} from "@angular/core";
import {DataSource} from "./data-source.service";
import {HttpParams} from "@angular/common/http";
import {BY_TOPIC} from "../app.tokens";
import {Observable} from "rxjs/observable";

import {Paragraph} from "../model/paragraph.model";

@Injectable()
export class ParagraphRepository {
  private readonly paragraphUrl: string = '/paragraph';

  constructor(private dataSource: DataSource){}

  byTopic(topicId: string): Observable<Paragraph[]> {
    return this.dataSource.get<Paragraph[]>(this.paragraphUrl,
      new HttpParams()
        .set(BY_TOPIC.key, BY_TOPIC.value)
        .set('value', topicId));
  }

}
