import {Injectable} from "@angular/core";
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {Login} from "../model/blogger.model";
import {Observable} from "rxjs";
import {map, tap} from "rxjs/internal/operators";
import {Router} from "@angular/router";
import {MatSnackBar} from "@angular/material";
import {SocialWrapper} from "../model/social.model";
import {DataSource} from "./data-source.service";

@Injectable()
export class AuthService {
  private readonly socialUrl: string = '/social';
  authenticated: boolean = false;

  constructor(private http: HttpClient,
              private dataSource: DataSource,
              private snackBar: MatSnackBar,
              private router: Router){}

  socials(): Observable<SocialWrapper> {
    return this.dataSource.get(`${this.socialUrl}/list`);
  }

  checkSocialLogin() {
    return this.dataSource.get(`${this.socialUrl}`).subscribe(
      response => {
        if(response['name']) {
          this.snackBar.open('Welcome Back! You are logged in by Social Login', null,{
            duration: 2000,
            horizontalPosition: 'end',
            verticalPosition: 'top'
          });
          this.authenticated = true;
        } else {
          this.authenticated = false;
        }
      }
    );
  }

  authenticate(login: Login): Observable<boolean> {
    return this.http.get('/rest/login', {
      headers: new HttpHeaders()
      .set("authorization","Basic " + btoa(login.email+":"+login.password))
    })
      .pipe(
        tap(response => {
          if(response['name']) {
            this.authenticated = true;
          } else {
            this.authenticated = false;
          }
        }),
        map(() => this.authenticated));
  }

  logout() {
    return this.http.post('/rest/logout',{})
      .subscribe(()=>{
        this.authenticated = false;
        this.router.navigateByUrl('/');
        this.snackBar.open('Logout Success!', null,{
          duration: 2000,
          horizontalPosition: 'end',
          verticalPosition: 'top'
        })
      });
  }
}
