import {Component, OnInit} from '@angular/core';
import {NavigationEnd, Router} from "@angular/router";
import 'rxjs/operators/filter';
import 'rxjs/operators/tap';
import {filter, map, tap} from "rxjs/internal/operators";

@Component({
  selector: 'app-locator',
  templateUrl: './locator.component.html',
  styleUrls: ['./locator.component.css']
})
export class LocatorComponent implements OnInit {
  paths: string[];

  constructor(private router: Router) {
    this.router.events.pipe(
      filter(event => event instanceof NavigationEnd),
      map((event: NavigationEnd) => event.urlAfterRedirects))
      .subscribe((path: string) => {
        this.separatePath(path)
      })
  }

  ngOnInit() {
  }


  private separatePath(path: string) {
    path = decodeURI(path);
    var reg = new RegExp('[?].+[=].+');
    let regResult: RegExpExecArray = reg.exec(path);

    if (regResult) {
      let index = reg.exec(path).index;
      path = path.substr(0, index);
    }

    let segments: string[] = path.split('/');
    segments[0] = '/';

    this.paths = segments;
  }
}
