import {Injectable} from "@angular/core";
import {DataSource} from "./data-source.service";
import {Observable} from 'rxjs/observable';
import {Topic} from "../model/topic.model";
import {HttpParams} from "@angular/common/http";
import {BY_CATEGORY, BY_SEARCH, TERM_ANY} from "../app.tokens";


@Injectable()
export class TopicRepository {
  private readonly topicUrl: string = 'topic';

  constructor(private dataSource: DataSource) {
  }

  byCategoryName(categoryName: string): Observable<Topic[]> {
    return this.dataSource.get(this.topicUrl,
      new HttpParams()
        .set(BY_CATEGORY.key, BY_CATEGORY.value)
        .set('value', categoryName));
  }

  byId(topicId: string): Observable<Topic> {
    return this.dataSource.get(`${this.topicUrl}/${topicId}`);
  }

  nextTopic(topic: Topic): Observable<Topic> {
    return this.dataSource.get(this.topicUrl,
      new HttpParams()
        .set('by','index')

        .set('value',topic.id + ""));
  }

  searchAny(value: string): Observable<Topic[]> {
    return this.dataSource.get(this.topicUrl,
      new HttpParams()
        .set(BY_SEARCH.key, BY_SEARCH.value)
        .set(TERM_ANY.key, TERM_ANY.value)
        .set('value', value));
  }

}
